.mobile__menu__container {
  display: none;
}

.mobile__menu__container.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
  display: block;
}

.menu__button {
  cursor: pointer;
  text-align: center;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  justify-content: center;
