  border-radius: 30px 0 30px 30px;
  margin-right: 20px;
  position: relative;
  overflow-y: auto;
  transform: translateY(150px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, .04), 0 2px 4px rgba(0, 0, 0, .08), 0 1px 3px rgba(0, 0, 0, .16);
}

@media (min-width: 768px) {
