.mobile__menu__close {
  cursor: pointer;
  z-index: 3;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  position: absolute;
  top: 10px;
  right: 24px;
  transform: translateX(100%);
}

.mobile__menu__item {
  display: flex;
}

.mobile__menu__link {
  width: 100%;
