  padding: 16px;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 19px;
  display: inline-block;
}

.mobile__menu__link:hover, .mobile__menu__link:focus {
  color: #000;
  z-index: 1;
  background-color: #bbf330;
  text-decoration: none;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: rgba(94, 99, 99, .7);
  transition: opacity .5s cubic-bezier(.4, 0, .2, 1), visibility .5s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.backdrop-mobile {
  width: 768px;
  width: 100%;
  height: 100%;
  z-index: 5;
  background-color: rgba(94, 99, 99, .7);
  transition: opacity .5s cubic-bezier(.4, 0, .2, 1), visibility .5s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

